#datetime-picker {
  margin-left: 100px;
  width: 200px;
  height: 50px;
  font-size: 20px;
  border-radius: 10px;
}
button {
  margin-left: 20px;
  width: 100px;
  height: 40px;
  font-size: 24px;
}
.timer {
  margin-top: 20px;
  margin-left: 100px;
  display: flex;
}
.field {
  margin-left: 10px;
}
.field:first-child {
  margin-left: 0;
}
span {
  font-size: 36px;
}
